<template>
  <div id="cart">
    <tab-control :titles="titles" @itemClick="itemClick"></tab-control>
    <content-message :ContMData="messages.bbc.list"></content-message>
    <!--<content-message :ContMData="messages[this.currentType].list"></content-message>-->
    <load-more @click.native="loadMore"></load-more>
    <advertisement></advertisement>
  </div>
</template>

<script>
  // 外部插件引用区
  // import {ajax} from "../../common/ajax";

  //公共组件区
  import TabControl from "../../components/content/tabControl/TabControl"
  import ContentMessage from "./childCopms/ContentMessage";
  import Advertisement from "../../components/content/advertisement/Advertisement";
  import LoadMore from "../../components/content/loadMore/LoadMore";


  //网络请求组件组
  import {getCartMessgaes} from "../../notework/request";


  export default {
    name: "category",
    components:{
      LoadMore,
      Advertisement,
      ContentMessage,
      TabControl
    },
    data(){
      return{
        titles:[{
          // "title": "VOA双语听力",
          "title": "VOA",
          "type": "bvoa"
        }, {
          // "title": "BBC双语听力",
          "title": "BBC",
          "type": "bbc"
        }],
        currentType:"bvoa",
        messages:{
          "bvoa":{page:0,list:[]},
          "bbc":{page:0,list:[]}
        }
      }
    },
    created(){

      this.getCartMessgaes("bvoa");
      this.getCartMessgaes("bbc");

    },
    // computed:{

    // },
    methods:{
      //时间监听相关方法
      itemClick(index){
        if (index === 0){
          this.currentType = "bvoa";
        }else if (index === 1){
          this.currentType = "bbc";
        }

        // let current = this.messages[currentCommentUserId];
        // this.getCategroyMessgaes(current);

      },
      loadMore(){
        let data=this.currentType;
        this.getCartMessgaes(data);
      },

      //网络请求相关方法
      getCartMessgaes(type){
        let Nsize = (this.messages[type].page + 1)*8;
        //   /*
        //   箭头函数被绑定到父级上下文，因此其中的this会指向父级组件，
        //   将回调函数中的function改成箭头函数，会将this从匿名对象重新指向外部的vue组件。
        //    */
        getCartMessgaes(type,Nsize,data =>{

          this.messages[type].list = data;
          this.messages[type].page +=1;


        })
      }
    }
  }
</script>

<style scoped>
  #cart {
    margin-top:44px ;

  }
  .content-message{
    min-height: 530px;
    /*background-color: #99ffa0;*/
  }

</style>
